.form-number{

    font-size: 1rem;
    padding: 10px 15px;
    border: none;
    font-weight:bold;
    font-size: 17px;
    padding:1rem;
    margin: 1rem;
    border: 1px solid rgb(202, 202, 202);
    transition: 0.15 ease-in-out;
}
.form-number:focus{
    outline: none;
    border: 2px solid rgb(202, 202, 202);

}

.invalid{

    font-size: 1rem;
    padding: 10px 15px;
    border: 2px solid red;


}
.invalid:focus{
    border: 2px solid red;

}

.form-label{
    font-weight: bold;
    font-family: 'Roboto';
}
.form-input{
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    font-size: 1.2rem;
    width: min(60vw,400px);
    margin: auto;
 
}
.form-input-col{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    font-size: 1.2rem;
    width: min(60vw,400px);
    margin: auto;
}

.generic-text-label{
    font-weight: bold;
    font-family: 'Roboto','sans-serif';
    justify-content: center;
    align-items: center;

    margin-top:1rem;
    margin-bottom: 1rem;
    font-size: 1.5rem;
}
.Dashboard-page{
    display: flex;
    justify-content: center;
    
}
*{
    font-family: 'Roboto';
    font-weight: bold;
    color:black;
}